import  React  from 'react';
import App from './App';
import { useSwipeable } from 'react-swipeable'

var swiper;

function Swiper() {
    const handlers = useSwipeable({
        onSwipedUp: () => onSwipeUp(),
        onSwipedDown: () => onSwipedDown(),
        onSwipedLeft: () => onSwipeLeft(),
        onSwipedRight: () => onSwipeRight(),
        preventDefaultTouchmoveEvent: true,
        trackMouse: true
    });

    swiper = React.createRef();

    return (
        <div { ...handlers }>
            <App ref={ swiper } />
        </div>
    );
}

function onSwipeUp() {
    document.getElementById("desc").classList.add("swiped-up");
    document.getElementById("image").classList.add("min");
    document.getElementById("profile-description").classList.add("maxed");
}

function onSwipedDown() {
    document.getElementById("desc").classList.remove("swiped-up");
    document.getElementById("image").classList.remove("min");
    document.getElementById("profile-description").classList.remove("maxed");
}

function onSwipeLeft() {
    swiper.current.handleLeftSwipe();
}

function onSwipeRight() {
    swiper.current.handleClick();
}

export default Swiper;
